.links, .sign-out{
    margin: auto;
}
.head{
    display: flex;
    align-items: center;
}
.header1{
    padding-right: 61px;
}
.header1 ul {
    display: flex;
    list-style: none;
    padding-left: 0;   
}
      
.header1 li {
    margin: 20px;
}
 .header1 a:hover{
    color: #FFFFFF;
    text-decoration: none;
  
 }
 
 .header1 a{ 
    color:#9A8CE8;
    font-size: 16px;
    letter-spacing: 3%;
    line-height: 24px;
    font-weight: 400;  
}
.header1 a.sign-out{
    color: #FFBB38;
    font-size: 13px;
    line-height: 19.5px;
    font-weight: 400;
}
.mission{
    color:white!important;
}